.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.page {
  cursor: pointer;
  border: 1px solid gray;
  padding: 3px 10px;
  font-size: 16px;
  border-radius: 8px;
  background: white;
  margin-right: 10px;
  margin-top: 4px;
}

.page-active {
  cursor: pointer;
  border: 1px solid#2C3034;
  padding: 3px 10px;
  font-size: 16px;
  border-radius: 8px;
  background: #2C3034;
  color: white;
  margin-right: 10px;
  margin-top: 4px;
}

.flex-wrap {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.search-input{
  float: right;
}